export function midPred(a: number, b: number, c: number) {
  if (a > b) {
    if (c > b) {
      if (c > a) {
        b = a
      }
      else {
        b = c
      }
    }
  }
  else {
    if (b > c) {
      if (c > a) {
        b = c
      }
      else {
        b = a
      }
    }
  }
  return b
}
